













import {
  defineComponent,
} from '@vue/composition-api';

import MaterialItem from './MaterialItem.vue';

export default defineComponent({
  components: {
    MaterialItem,
  },
  props: {
    materialList: {
      type: Array,
      default: () => [],
    },
    disabledInfiniteScroll: {
      type: Boolean,
      default: false,
    },
    displayMode: {
      type: String as () => 'drape' | 'flat',
      default: 'drape',
    },
  },
  setup(props, { root }) {
    async function preview(index: number) {
      let srcList;
      if (props.displayMode === 'flat') {
        srcList = props.materialList.map((material: any) => material.imgJson?.texture || material.imgJson?.flat);
      } else {
        srcList = props.materialList.map((material: any) => material.imgJson.thumbnail);
      }
      // @ts-ignore
      await root.$ImageViewer.show(srcList, {
        index,
        targetElems: '[data-img-type=\'material-item\']',
      });
    }

    return {
      preview,
    };
  },
});
